*, *::before, *::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body, div, dl, dt, dd, ul, ol, li,
h1, h2, h3, h4, h5, h6, pre, code,
form, fieldset, legend, input, button,
textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0; }

a, a:hover, a:active, a:focus, input, input:hover, input:active, input:focus {
  outline: none; }

ul, ol {
  list-style: none; }

#app {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: #000;
  display: flex;
  flex-direction: column; }
  #app .body {
    position: relative;
    flex: 1;
    width: 100vw;
    background: #000; }
    #app .body #canvas {
      display: none; }
    #app .body #video {
      height: 100%;
      width: 100%;
      background: #000; }
    #app .body .photos {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      background: #fff;
      transform: translateX(100%);
      transition: all 0.3s; }
      #app .body .photos.grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-auto-rows: 10px;
        grid-column-gap: 5px;
        height: e("calc(100% - 32px)");
        overflow-y: auto; }
      #app .body .photos.active {
        transform: translateX(0); }
      #app .body .photos .close {
        position: absolute;
        top: 0;
        right: 0; }
      #app .body .photos .item img {
        width: 100%;
        height: auto; }
  #app .footer {
    height: 130px; }
    #app .footer .action {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 70%;
      margin: 10px 15% 50px; }
      #app .footer .action .btn {
        border-radius: 50%;
        border: 1px solid #fff;
        width: 50px;
        height: 50px; }
      #app .footer .action .photo {
        position: relative;
        overflow: hidden; }
        #app .footer .action .photo img {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%; }
      #app .footer .action .capture {
        width: 70px;
        height: 70px;
        background: #fff; }
